<script>
  //require('@/mock/index.js')
  import HomeBar from "../../components/common/HomeBar";
  import Scroll from "../../components/common/Scroll";
  import HotSearch from "../../components/home/hotSearch";
  import IsLoading from "../../components/common/IsLoading";
  import {getHomeData} from "@/api/api.js"
  export default {
    name: "BookHome",
    data(){
      return{
        search:null,
        ifShowSearch:false,
        ifShowSearchPage:false,
        isFlapPage:false,
        random:null
      }
    },
    mounted(){

    },
    components: {IsLoading, HotSearch, Scroll, HomeBar},
    methods:{
      onScroll(scrollTop){
        console.log(scrollTop)
        if(scrollTop>0){
          this.ifShowSearch = true
        }else {
          this.ifShowSearch = false
        }
      },
      showSearchPage(){
        this.ifShowSearch = true
        this.ifShowSearchPage = true
      },
      back(){
        if(this.ifShowSearchPage ){
          this.ifShowSearchPage = false
          //this.ifShowSearch = false
        }else {
          this.$router.back(-1)
        }
      },
      showFlapPage(){
        this.isFlapPage=true
        //this.random = null
        getHomeData().then(res=>{
          const data = res.data
          this.random = res.data.random[Math.floor(Math.random()*data.random.length)]
          console.log(this.random )
        })
      },
      closeFlap(){
        this.isFlapPage = false
      },
      readNow(){
          const bookPath = this.random.categoryText + '|' + this.random.fileName + '.epub'
          this.$router.push("/ebook/"+bookPath)
      }
    },
    activated() {
      console.log(1)
    }
  }
</script>

